<template>
  <div>
    <header-page title="Livros"></header-page>
    <div class="home mt-2">
        <b-container>
            <b-button variant="primary" class="mb-2 mx-auto" @click="adicionarLivro = !adicionarLivro">
                    Adicionar Livros
            </b-button>
            <adicionar-livro v-if="adicionarLivro" v-on:cadastrou="adicionarLivro = !adicionarLivro"></adicionar-livro>
            <b-card border-variant="primary" v-if="!contemLivros"
                header="Nenhum Livro registrado"
                header-bg-variant="primary"
                header-text-variant="white"
                align="center">
                <b-button variant="primary" class="mb-2 mx-auto" @click="livros = !livros">
                        Adicionar Livros
                </b-button>
            </b-card>
            <b-card border-variant="primary" v-if="contemLivros"
                :header="livro.titulo"
                header-bg-variant="primary"
                header-text-variant="white"
                align="center"
                class="mt-1" v-for="livro of listaLivros" :key="livro.id">
                <b-list-group>
                    <b-list-group-item>Páginas: {{livro.paginas}}</b-list-group-item>
                    <b-list-group-item>Páginas Lidas: {{livro.paginasLidas}}</b-list-group-item>
                    <b-list-group-item>Páginas Restantes: {{livro.paginasRestantes}}</b-list-group-item>
                </b-list-group>
                <b-button variant="primary" class="mt-2 mx-auto" @click="deletarLivro(livro.id)">
                        Excluir
                </b-button>
            </b-card>
        </b-container>
    </div>
  </div>
</template>

<script>
// @ is an alias to /src
import AdicionarLivro from '@/components/AdicionarLivro.vue'
import HeaderPage from '@/components/HeaderPage'

export default {
  name: 'home',
  components: {
    HeaderPage,
    AdicionarLivro
  },
  data(){
      return{
          livros: true,
          adicionarLivro: false
      }
  },
  methods:{
    deletarLivro(item){
        this.$store.commit('DeletarLivro', item)
  
      }
  },
  computed:{
      listaLivros(){
          return this.$store.getters.listaCompleta
      },
      contemLivros(){
        if(this.listaLivros == "")
            return false
        else
            return true
      }
  }
}
</script>
