import Vue from 'vue'
import Vuex from 'vuex'
import storage from 'localforage'
Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    books:{
      list:[],
    },
    especificacoes:{
      dataTermino: '',
      limitePaginasDiarias: 10
    },
    metas:{
      totalPaginas:0,
      paginasDiarias:0
    }
  },
  mutations: {
    AddList(state, books){
      state.books.list.push(books)
    },
    AddBook(state, book){
      console.log(book)
      let livro = {
        titulo: book.titulo,
        paginas: book.paginas,
        paginasLidas: book.paginasLidas,
        paginasRestantes: (book.paginas - book.paginasLidas),
        id: book.id
      }
      state.books.list.push(livro)
      storage.setItem(book.id, livro)
    },
    AddLeitura(state, leitura){
      let pos = state.books.list.map((e)=>{
        return e.id
      }).indexOf(leitura.id)
      let book = {}
      let livro = state.books.list[pos]
      //console.log(livro)
      //console.log(leitura)
      book.id = livro.id
      book.titulo = livro.titulo
      book.paginas = livro.paginas
      book.paginasLidas = (parseInt(livro.paginasLidas) + parseInt(leitura.paginasLidas))
      book.paginasRestantes=(livro.paginas - book.paginasLidas)
      console.log(book)
      state.books.list.splice(pos, 1, book)
      storage.removeItem(book.id)
      storage.setItem(book.id, book)
    },
    AddEspecificacoes(state, especificacoes){
      storage.setItem('especificacoes', especificacoes)
      console.log("ok")
      state.especificacoes.dataTermino = especificacoes.dataTermino
      state.especificacoes.limitePaginasDiarias = especificacoes.limiteDiario
      console.log(especificacoes)
    },
    DeletarLivro(state, id){
      let pos = state.books.list.map((e)=>{
        return e.id
      }).indexOf(id)
      state.books.list.splice(pos, 1)
      storage.removeItem(id)
    },
    PreencherLivrosStorage(state, lista){
      state.books.list = lista
    }
  },
  getters:{
    listaCompleta(state){
     return state.books.list
    },
    especificacoes(state){
      return state.especificacoes
    }
  }
})
