<template>
  <div>
    <header-page title="Cadastro de Leitura"></header-page>
    <div class="page">
      <b-container>
          <b-card border-variant="primary"
          header="Cadastre a leitura que você acabaou de realizar"
          header-bg-variant="primary"
          header-text-variant="white"
          align="center"
          class="mt-2">
          <b-form class="mt-5">
              <b-form-group label="Livro:">
                    <b-form-select v-model="leitura.id" :options="listaLivros" class="mb-3" />
              </b-form-group>
              <b-form-group 
                        label="Páginas Lidas:"
                        >
              <b-form-input 
                          type="number"
                          v-model="leitura.paginasLidas"
                          required
                          placeholder="0">
              </b-form-input>
              </b-form-group>
              
              <b-button variant="primary" @click.prevent="cadastrar">Cadastrar</b-button>
            </b-form>  
  </b-card>
        
      </b-container>
    </div>
  </div>
</template>

<script>
// @ is an alias to /src
import HeaderPage from '@/components/HeaderPage'

export default {
  name: 'home',
  components: {
    HeaderPage
  },
  data(){
      return{
        leitura:{
          id: 0,
          paginasLidas: '',
        },
      }
  },
  computed:{
    listaLivros(){
      let livros = []
      this.$store.getters.listaCompleta.forEach(e => {
        livros.push({value: e.id, text: e.titulo})
      });
      return livros
    }
  },
  methods:{
    cadastrar(){
      console.log(this.leitura)
      const clone = JSON.parse(JSON.stringify(this.leitura))
      console.log(clone)
      this.$store.commit('AddLeitura', clone)
      
      this.resetForm()
       this.$toasted.show("Cadastrado com sucesso", { 
            theme: "bubble", 
            position: "top-center", 
            duration : 5000
        });
    },
    resetForm(){
      this.leitura = {
        id: '',
        paginasLidas: ''
      }
    }
  }
}
</script>
