import Vue from 'vue'
import Router from 'vue-router'
import Home from './views/Home.vue'
import Metas from './views/Metas.vue'
import Especificacoes from './views/Especificacoes.vue'
import Livros from './views/Livros.vue'
import Manual from './views/Manual.vue'
import CadastroLeitura from './views/CadastroLeitura.vue'

Vue.use(Router)

export default new Router({
  mode: 'history',
  base: process.env.BASE_URL,
  routes: [
    {
      path: '/',
      name: 'home',
      component: Livros
    },
    {
      path: '/metas',
      name: 'metas',
      component: Metas
    },
    {
      path: '/especificacoes',
      name: 'especificacoes',
      component: Especificacoes
    },
    {
      path: '/livros',
      name: 'livros',
      component: Livros
    },
    {
      path: '/cadastro-leitura',
      name: 'cadastro-leitura',
      component: CadastroLeitura
    },
    {
      path: '/manual',
      name: 'manual',
      component: Manual
    },
  ]
})
