<template>
    <div>
        <b-form inline class="mt-1 mb-2"> 
            <label class="sr-only" >Título</label>
            <b-input class="mb-2 mr-sm-2 mb-sm-0" placeholder="Título" v-model="livro.titulo"/>
            <label class="sr-only" >Páginas</label>
            <b-input-group class="mb-2 mr-sm-2 mb-sm-0">
                <b-input placeholder="Páginas" v-model="livro.paginas" />
            </b-input-group>
            <b-button variant="primary" @click.prevent="salvar">Salvar</b-button>
        </b-form>
    </div>
</template>
<script>
    import moment from 'moment'
  export default {
    name: 'home',
    components: {
    },
    data(){
        return{
          livro:{
              titulo: '',
              paginas: '',
              paginasLidas: 0,
          },
            adicionarLivro: true
        }
    },
    methods:{
        
    salvar(){
        const clone = JSON.parse(JSON.stringify(this.livro))
        clone.id = new Date().getUTCMilliseconds();
        this.$store.commit('AddBook', clone)
        this.resetForm()
         this.$toasted.show("Salvo com sucesso!", { 
            theme: "bubble", 
            position: "top-center", 
            duration : 5000
        });
        this.$emit('cadastrou')
      },
      resetForm(){
        this.livro = {
              titulo: '',
              paginas: '',
              paginasLidas: 0,
          }
      },
    }
  }
</script>