<template>
  <div>
    <header-page title="Metas"></header-page>
    <div class="home">
        <b-container class="text-center">
            <b-row class="mt-1 ml-md-auto min">
                <b-col sm="12">
                    <b-card bg-variant="info"
                        title="Total de Páginas Para Ler"
                        class="text-center">
                        
                        <h2 class="card-text">{{totalPaginas}}</h2>
                    </b-card>
                </b-col>
                <b-col sm="12">
                    <b-card bg-variant="info"
                        title="Quantidade de Leitura Diária"
                        class="text-center mt-1">
                        
                        <h2 class="card-text">{{diaria}}</h2>
                    </b-card>
                </b-col>
                <b-col sm="12">
                    <b-card bg-variant="info"
                        title="Data Para Término da Leitura"
                        class="text-center mt-1">
                        
                        <h2 class="card-text">{{dataTermino || "Defina Sua Especificações"}}</h2>
                    </b-card>
                </b-col>
            </b-row>
        </b-container>
    </div>
  </div>
</template>

<script>
// @ is an alias to /src
import HelloWorld from '@/components/HelloWorld.vue'
import HeaderPage from '@/components/HeaderPage'
import moment from 'moment'

export default {
  name: 'home',
  components: {
    HeaderPage
  },
  mounted() {
      if(this.limiteLeitura < this.diaria){
          this.$toasted.show("Você está atrasado de acordo com seu limite de leitura diário", { 
            theme: "bubble", 
            position: "top-center", 
            duration : 5000
        });
      }
  },
  data(){
      return{
    
      }
  },
  computed:{
      listaLivros(){
          return this.$store.getters.listaCompleta
      },
      totalPaginas(){
        let total = 0      
          this.$store.getters.listaCompleta.forEach(element => {
            total += element.paginasRestantes
          });
        return total
      },
    limiteLeitura(){
      return this.$store.getters.especificacoes.limitePaginasDiarias
    },
    dataTermino(){
      return moment(this.$store.getters.especificacoes.dataTermino).format("DD/MM/YYYY")
    },
    diaria(){
      moment.locale('pt-br')
      let data1 = moment(this.$store.getters.especificacoes.dataTermino)
      let data2 = moment()
      let diff = data1.diff(data2, 'days')
      console.log(this.totalPaginas)
      let diaria = Math.ceil(this.totalPaginas / diff)
      if(isNaN(diaria))
        return "Alguma informação está faltando"
        console.log(diaria)
      return diaria
    }
  }
}
</script>

