<template>
  <div id="app">
    <div id="nav">
      <nav-bar></nav-bar>
    </div>
      <router-view/>
  </div>
</template>

<script>
  import NavBar from './components/NavbarComponent'
  import storage from 'localforage'
  export default {
  components: {
    NavBar
  },
  data(){
    return{
    }
  },
  mounted() {
    let lista = []
    storage.keys().then(function(keys){
      keys.forEach(element => {
        if(element != "especificacoes"){
          storage.getItem(element).then(function(value){
            lista.push(value)
          }) 
          
        }
      });
      console.log(lista)

    })
  this.$store.commit('PreencherLivrosStorage', lista)
  },
}
</script>

