<template>
  <div>
    <header-page title="Especificações"></header-page>
    <div class="page">
      <b-container>
          <b-card border-variant="primary"
          header="Suas informações para funcionamento do sistema"
          header-bg-variant="primary"
          header-text-variant="white"
          align="center"
          class="mt-2">
          <b-form class="mt-5">
              <b-form-group 
                            label="Data Para Término:"
                            >
                <b-form-input 
                              type="date"
                              
                              required
                              v-model="especificacoes.dataTermino"
                              placeholder="Data">
                </b-form-input>
              </b-form-group>
              <b-form-group 
                        label="Seu limite de leitura diário:"
                        >
              <b-form-input 
                          type="number"
                          
                          required
                          v-model="especificacoes.limiteDiario"
                          placeholder="0">
              </b-form-input>
              </b-form-group>
              
              <b-button variant="primary" @click.prevent="salvar">Atualizar</b-button>
            </b-form>  
  </b-card>
        
      </b-container>
    </div>
  </div>
</template>

<script>
// @ is an alias to /src
import HeaderPage from '@/components/HeaderPage'
import * as moment from 'moment';
export default {
  name: 'home',
  components: {
    HeaderPage
  },
  data(){
    return{
      especificacoes: {limiteDiario: null, dataTermino: moment()},
    }
  },
  methods:{
    salvar(){
      const clone = JSON.parse(JSON.stringify(this.especificacoes))
      this.$store.commit('AddEspecificacoes', clone)
      this.resetForm()
       this.$toasted.show("Salvo com sucesso!", { 
            theme: "bubble", 
            position: "top-center", 
            duration : 5000
        });
    },
    resetForm(){
      this.especificacoes = {
        limiteDiario: null,
        dataTermino: moment()
      }
    },
  }
}
</script>
